  putData = () => {
    let data = {
      id: this.props.location.id,
      history: this.state.history,
      count: this.state.count,
    };
    console.log(data)

    console.log(data);
    fetch('https://www.sevenhero.cn:2050/updateobject', {
      method: 'POST',
      headers: {
        'content-type': 'text/plain'
      },
      body: JSON.stringify(data)
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
      } else {
        //  return res//成功后的返回值
        return res;
      }

    }).then(d => {
      //'提交成功'
      if (d.status === 200) {

        alert('更新成功')
      }
      else {
        alert('更新失败')
      }
      // this.setState({
      //   obj:'',
      //   count:'',
      //   probability:'',
      //   src:'',
      //   files:[]

      // })

    }).catch(err => {
      alert(err.message)
    })
  }
  componentDidMount = () => {
    let id = this.props.location.id;
    fetch('https://www.sevenhero.cn:2050/users/' + id).then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        gamename: d[0].gamename,
        orgation: d[0].orgation,
        history: d[0].history,
        count: d[0].count,
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
  }
  render() {
    console.log(this.props);
    return (
      <div>

        <Button type="primary" onClick={this.showModal}>显示对话框</Button>
        <Modal title="第一个 Modal" visible={this.state.visible}
          onOk={this.handleOk} onCancel={this.handleCancel}
        >
           <Form horizontal onSubmit={this.handleSubmit} >
          <FormItem
            label="InputNumber 数字输入框"
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 10 }}
          >
            <InputNumber min={1} max={10} style={{ width: 100 }}
              
            />
            <span className="ant-form-text"> 台机器</span>
          </FormItem>
          <FormItem
            label="用户名"
          >
          <p className="ant-form-text" id="userName" name="userName">SSS</p>
          </FormItem>
          <FormItem
            // {...formItemLayout}
            label="备注"
            help="随便写点什么"
          >
            <Input type="textarea" placeholder="随便写"
            // {...getFieldProps('remark', { initialValue: '' })} 
            />
          </FormItem>

        </Form>
         
         
        </Modal>
      </div>
    )
  }
