import userEvent from '@testing-library/user-event';
import React from 'react';
import {HashRouter as Router,Redirect,Route,Switch} from 'react-router-dom';
import addObj from './addObj';
import addUser from './addUser';
import Login from './Login';
import Ownobj from './Ownobj';
import updateUser from './updateUser';
import User from './User';
import Object1 from './Object1';




const App = ()=>{
	return <Router>
		
			<Switch>
                {/*登录  */}
			<Route path='/login' component={Login}/>
            {/*  用户*/}
			<Route path='/user' component={User}/>
            {/*添加用户  */}
			<Route path='/adduser' component={addUser}/>
            {/* 更新用户 */}
			<Route path='/update' component={updateUser}/>
            {/* 奖品 */}
			<Route path='/object' component={Object1}/>
             {/*获得 奖品 */}
			<Route path='/addobj' component={addObj}/>
            {/*  获得奖品*/}
			<Route path='/ownobj' component={Ownobj}/>


			</Switch>	
			<Redirect to='/login'/>
		</Router>
}
export default App;

















